(function($) {

    $(window).on("load", function() {
        $('.load').css('opacity', '1');
    });


    // BUTTONS
    $(document).on('click', '#scrollDown, body.page-template-template-page-subsidies-cat .hero .cnt .btn-purple, body.page-template-template-page-subsidies .hero .cnt .btn-purple', function() {
        $('html, body').animate({
            scrollTop: $("#next").offset().top - 100
        }, 750);
        return false
    });

    // HERO SCROLL LINK
    $("body.single-subsidies .hero .img ul li a").click(function () {
        $('html,body').animate({ scrollTop: $(this.hash).offset().top }, 1000);
    });

    $('#scrollDown').delay(1000).animate({ opacity: 1 }, 500);

    // SCROLL TOP
    $(".btn-top").click(function() {
        $("html").animate({ scrollTop: 0 }, "slow");
    });


    // TABS SLIDER HOME
    $('.hero-slider-slide').hide();
    $('.hero-slider-slide:first').show();
    $('.hero-slider-thumbs div:first').addClass('active');
        
    $('.hero-slider-thumbs div').click(function(){
        var t = $(this).attr('id');
        $('.hero-slider-thumbs div').removeClass('active');           
        $(this).addClass('active');
        
        $('.hero-slider-slide').hide();
        $('#'+ t + 'C').fadeIn('slow');
    });

    // DOTACJE KOLO
    $(document).on('click', '#showProject01', function() {
        $('.grand .grand-apla').addClass('show');
        $('.project').removeClass('active');
        $('#project-1').addClass('active');
        $('.grand .grand-col:first-child').addClass('small');
    });
    $(document).on('click', '#showProject02', function() {
        $('.grand .grand-apla').addClass('show');
        $('.project').removeClass('active');
        $('#project-4').addClass('active');
        $('.grand .grand-col:first-child').addClass('small');
    });
    $(document).on('click', '#showProject03', function() {
        $('.grand .grand-apla').addClass('show');
        $('.project').removeClass('active');
        $('#project-3').addClass('active');
        $('.grand .grand-col:first-child').addClass('small');
    });
    $(document).on('click', '#showProject04', function() {
        $('.grand .grand-apla').addClass('show');
        $('.project').removeClass('active');
        $('#project-2').addClass('active');
        $('.grand .grand-col:first-child').addClass('small');
    });

    $(document).on('click', '.grand .small button', function() {
        $('.grand .grand-apla').removeClass('show');
        $('.project').removeClass('active');
        $('.grand .grand-col:first-child').removeClass('small');
    });


    // ALL

    if ('loading' in HTMLImageElement.prototype) {
        const images = document.querySelectorAll('img[loading="lazy"]');
        images.forEach(img => {
            img.src = img.dataset.src;
        });
    } else {
        // Dynamically import the LazySizes library
        const script = document.createElement('script');
        script.src =
            'https://cdnjs.cloudflare.com/ajax/libs/lazysizes/5.1.2/lazysizes.min.js';
        document.body.appendChild(script);
    }

    // $("#menu-item-24").load('/wp-content/themes/frontline/template-parts/partial-submenu.php');

        $( "#menu-item-24" ).hover(
      function() {
        $('#submenu').addClass('submenuOn');
        $('.overlay').addClass('overlayOn');
        $('body').addClass('is-popup')
      }, function() {
        $( '#submenu' ).removeClass('submenuOn');
        $('.overlay').removeClass('overlayOn');
        $('body').removeClass('is-popup')
      });

        $( "#submenu" ).hover(
      function() {
        $(this).addClass('submenuOn');
        $('.overlay').addClass('overlayOn');
        $('body').addClass('is-popup')
      }, function() {
        $(this).removeClass('submenuOn');
        $('.overlay').removeClass('overlayOn');
        $('body').removeClass('is-popup')
      });

    // PLACEHOLDER
    $('input,textarea').focus(function() {
        $(this).data('placeholder', $(this).attr('placeholder'))
        .attr('placeholder', '');
        }).blur(function() {
        $(this).attr('placeholder', $(this).data('placeholder'));
    });


    // MENU SIZE - SCROLL DOWN
    var menu = $('body').offset().top + 150
    if ($(window).width() > 992) {

        $(window).scroll(function() {
            if ($(this).scrollTop() >= menu) {
                $('.navbar__fixed').addClass('navbar__fixed--small');
                $('.mobile-overlay').addClass('mobile-overlay--scroll');
            } else {
                $('.navbar__fixed').removeClass('navbar__fixed--small');
                $('.mobile-overlay').removeClass('mobile-overlay--scroll');
            }
        });
    }

    $(document).on('click', '#menu-item-24 a', function() {
        return false;
    });

    // var submenu = $('.submenu');
    // submenu.hide();

    // $("#menu-item-24 a").hover(function() {
    //     submenu.show();
    //     return false;
    // });




}(jQuery));